import { useState } from "react";
import { useNavigate } from "react-router-dom";

const WorkCard = (work) => {

    const workData = work.data;
    const [cardStyle] = useState(work.style);
    const [description] = useState(workData.shortdescription);

    const navigate = useNavigate();

    function WorkClicked() {
        navigate("/kieranportfolio/workpage", {state:{data: workData}});
    }

    return ( 
        <button onClick={WorkClicked} className={cardStyle}>
            <div className="workimagecontainer">
                <img src={workData.mainimage} alt="" />
            </div>
            <div className="workinfocontainer">
                <h1>{workData.title}</h1>
                <hr></hr>
                <p>{description}</p>
            </div>
        </button>
     );
}
 
export default WorkCard;